import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import { Button } from "primereact/button";

import styles from "./BookDetail.module.css";

function BookDetail() {
  const { id } = useParams();
  const fetchUrl = `${process.env.REACT_APP_SERVER_DOMAIN}/books/book_details/${id}`;

  const [bookData, setBookData] = useState({});

  useEffect(() => {
    const fetchBook = async () => {
      const result = await axios.get(fetchUrl);
      setBookData(result.data);
    };
    fetchBook();
  }, []);

  return (
    <div className={styles.outerContainer}>
      <div className={styles.innerContainer}>
        <div className="p-d-flex p-jc-center">
          <div className={styles.imageContainer}>
            <img alt={bookData.title} src={bookData.image_src} />
          </div>
          <div className={styles.contentContainer}>
            <div className={styles.cardTitle}>{bookData.title}</div>
            <div className={styles.cardAuthor}>Author: {bookData.author}</div>
            <div className={styles.cardPrice}>Price: ${bookData.price}</div>
            <div className={styles.additionalInfo}>Additional Information</div>
            <div>ISBN 13: <b>{bookData.isbn_13}</b></div>
            <div>Publisher: <b>{bookData.publisher}</b></div>
            <div>Publish Date: <b>{bookData.publish_date}</b></div>
            <div>Pages: <b>{bookData.pages}</b></div>
            <div className={styles.cardButtonContainer}>
              <Button
                label="Add to Cart"
                icon="pi pi-check-circle"
                className={styles.addCartButton}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BookDetail;
